import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { ListItem, ListInfo, LoadMore } from "../style";
import { actionCreators } from "../store";
import { Link } from "react-router-dom";

class List extends PureComponent {
  render() {
    const { list, getMoreList, articlePage } = this.props;
    return (
      <div>
        {list.map((item, index) => {
          return (
            <ListItem key={index}>
              <Link className="wrap-img" to={"/detail/" + item.get("id")}>
                <img
                  className="img-blur-done"
                  src={item.get("imgUrl")}
                  alt="120"
                />
              </Link>
              <ListInfo>
                <Link className="title" to={"/detail/" + item.get("id")}>
                  {item.get("title")}
                </Link>
                <p className="abstract">{item.get("abstract")}</p>
                <div className="meta">
                  <a
                    className="nickname"
                    target="_blank"
                    href="/u/d248768a621a"
                  >
                    {item.get("nickname")}
                  </a>
                  <a target="_blank" href="/p/d000d9402e14#comments">
                    <i className="iconfont ic-list-comments" /> 21
                  </a>
                  <span>
                    <i className="iconfont ic-list-like" /> 26
                  </span>
                  <span>
                    <i className="iconfont ic-list-money" /> 1
                  </span>
                </div>
              </ListInfo>
            </ListItem>
          );
        })}

        <LoadMore onClick={() => getMoreList(articlePage)}>阅读更多</LoadMore>
      </div>
    );
  }
}

const mapState = state => ({
  list: state.get("home").get("articleList"),
  articlePage: state.getIn(["home", "articlePage"])
});

const mapDispatch = dispatch => ({
  getMoreList(articlePage) {
    dispatch(actionCreators.getMoreList(articlePage));
  }
});

export default connect(
  mapState,
  mapDispatch
)(List);
