import styled from "styled-components";

export const HomeWrapper = styled.div`
  overflow: hidden;
  width: 960px;
  margin: 0 auto;
`;

export const HomeLeft = styled.div`
  float: left;
  margin-left: 15px;
  margin-top: 30px;
  width: 625px;
  .banner-img {
    width: 625px;
    height: 270px;
    margin-bottom: 35px;
  }

  .split-line {
    margin: -5px 0 15px;
    border-top: 1px solid #f0f0f0;
  }
`;

export const HomeRight = styled.div`
  width: 280px;
  float: right;
`;

export const TopicWrapper = styled.div`
  overflow: hidden;
  margin-bottom: 20px;
`;

export const TopicItem = styled.div`
  float: left;
  height: 32px;
  margin: 0 18px 18px 0;
  line-height: 32px;
  background: #f7f7f7;
  font-size: 14px;
  color: #000;
  padding-right: 10px;
  border: 1px solid #dcdcdc;
  border-radius: 4px;
  .topic-pic {
    display: block;
    float: left;
    width: 32px;
    height: 32px;
    margin-right: 10px;
  }
`;

export const ListItem = styled.div`
  position: relative;
  border-bottom: 1px solid #f0f0f0;
  padding: 15px 2px 20px 0;
  margin: 0 0 15px;
  .wrap-img {
    width: 150px;
    height: 100px;
    position: absolute;
    right: 0;
    top: 50%;
    margin-top: -60px;
  }

  img {
    width: 100%;
    height: 100%;
    border-radius: 4px;
    border: 1px solid #f0f0f0;
  }
`;

export const ListInfo = styled.div`
  padding-right: 165px;

  .title {
    font-weight: bolder;
    color: #333;
    font-size: 18px;
    line-height: 1.5;
    display: inherit;
  }

  .abstract {
    font-size: 13px;
    line-height: 24px;
    color: #999;
  }

  .meta {
    padding-right: 0 !important;
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
  }

  .meta a,
  .meta span {
    margin-right: 10px;
    color: #b4b4b4;
  }
`;

//
export const RecommendWrapper = styled.div`
  width: 100%;
  padding: 30px 0 0;
  margin-top: -4px;
  padding-bottom: 4px;
  min-height: 228px;
`;

export const RecommendItem = styled.a`
  img {
    width: 100%;
    min-height: 50px;
    margin-bottom: 6px;
    border-radius: 4px;
  }
`;

//load more
export const LoadMore = styled.div`
  width: 100%;
  height: 40px;
  line-height: 40px;
  background: #a5a5a5;
  text-align: center;
  border-radius: 20px;
  color: #fff;
  margin: 30px 0;
  cursor: pointer;
`;

//back top

export const BackTop = styled.div`
  position: fixed;
  right: 100px;
  bottom: 100px;
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;

  border: 1px solid #dcdcdc;
  background-color: #fff;
  transition: 0.1s ease-in;
  cursor:pointer;
`;
