import axios from "axios";
import * as constants from "./constants";

const changeLogin = () => ({
  type: constants.CHANGE_LOGIN,
  value: true
});

export const logout = () => ({
  type: constants.CHANGE_LOGOUT,
  value: false
});

export const login = (username, password) => {
  return dispatch => {
    axios
      .get("/api/login.json", {
        params: {
          username: username,
          password: password
        }
      })
      .then(function(res) {
        dispatch(changeLogin());
      })
      .catch(function(error) {
        console.log(error);
      });
  };
};
