import axios from "axios";
import * as constants from "./constants";

const changeDetail = (title, author, content) => ({
  type: constants.CAHNGE_DETAIL,
  title,
  author,
  content
});

export const getDetail = id => {
  return dispatch => {
    console.info(id);
    axios
      .get("/api/detail.json", {
        params: {
          id: id
        }
      })
      .then(function(res) {
        //console.info(res.data.data);
        const result = res.data.data;
        dispatch(changeDetail(result.title, result.author, result.content));
      })
      .catch(function(error) {
        console.info(error);
      });
  };
};
